import { useState } from "react";
import { Chart2 } from "./Chart";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import "./styles.css";
import moment from "moment";
import html2canvas from "html2canvas";
import pdfConverter from "jspdf";

export default function App() {
  // const [chartData, setChartData] = useState({});
  //const [url, setUrl] = useState(null);
  const [displayValues, setDisplayValues] = useState(false);
  const [fromTime, setFromTime] = useState("");
  const [toTime, setToTime] = useState("");
  const [furnace, setFurnace] = useState("");
  const [customer, setCustomer] = useState("");
  const [material, setMaterial] = useState("");
  const [partName, setPartName] = useState("");
  const [partNumber, setPartNumber] = useState("");
  const [process, setProcess] = useState("");
  const [batchNo, setBatchNo] = useState("");
  const [batchQty, setBatchQty] = useState("");
  const [soakingStart, setSoakingStart] = useState("");
  const [soakingEnd, setSoakingEnd] = useState("");
  const [totalSoakingTime, setTotalSoakingTime] = useState("");
  const [subTitle, setSubTitle] = useState("");
  const [interval, setInterval] = useState("");

  const handleChangeFromTime = (data) => {
    // setChartData({});
    setFromTime(data);
  };
  const handleChangeToTime = (data) => {
    // setChartData({});
    setToTime(data);
  };

  const options = [
    //actual,set [ft,ot,cv]
    { value: "CH1,CH15,CH3,CH19,CH2,CH16", label: "SQF1" },
    { value: "CH4,CH18,CH6,CH20,CH5,CH17", label: "SQF2" },
    { value: "CH7,CH24,CH100,CH1,CH1,CH1", label: "TF1" },
    { value: "CH8,CH25,CH1,CH1,CH1,CH1", label: "TF2" },
    { value: "CH9,CH28,CH1,CH1,CH1,CH1", label: "TF3" },
    { value: "CH10,CH22,CH12,CH21,CH11,CH23", label: "GCF" },
    { value: "CH13,CH27,CH1,CH1,CH1,CH1", label: "THF1" },
    { value: "CH14,CH26,CH1,CH1,CH1,CH1", label: "THF2" }
  ];

  let makeSubTitle = () => {
    const fromTimeToDisplay = fromTime
      ? moment(fromTime).format("DD/MM/YYYY")
      : "";
    const toTimeToDisplay = toTime ? moment(toTime).format("DD/MM/YYYY") : "";
    const fromString = fromTimeToDisplay ? "From: " + fromTimeToDisplay : "";
    const toString = toTimeToDisplay ? " | To: " + toTimeToDisplay + " | " : "";
    const customerString = " Customer: " + customer;
    const materialString = " | Material: " + material;
    const partNameString = " | Part Name: " + partName;
    const partNumberString = " | Part Number: " + partNumber;
    const processString = " | Process: " + process;
    const batchNoString = " | Batch No: " + batchNo;
    const batchQtyString = " | Batch Qty: " + batchQty;
    const soakingStartString = " | Soaking Start: " + soakingStart;
    const soakingEndString = " | Soaking End: " + soakingEnd;
    const totalSoakingTimeString = " | Total Soaking Time: " + totalSoakingTime;

    setSubTitle(
      fromString +
        toString +
        customerString +
        materialString +
        partNameString +
        partNumberString +
        processString +
        batchNoString +
        batchQtyString +
        soakingStartString +
        soakingEndString +
        totalSoakingTimeString
    );
  };

  const getTimePortion = (item) => {
    let time = item.timestamp;
    return time.substring(11, 16);
  };

  const exportChart = (e) => {
    //const but = e.target;
    //but.style.display = "none";
    let input = window.document.getElementsByClassName("App ChartDiv")[0];

    html2canvas(input).then((canvas) => {
      console.log(canvas.width + "  " + canvas.height);
      const img = canvas.toDataURL("image/jpg");
      const pdf = new pdfConverter(
        "l",
        "pt",
        [canvas.width, canvas.height],
        true
      );
      pdf.addImage(img, "JPEG", 1, 1, canvas.width, canvas.height);
      pdf.save("chart.pdf");
      //but.style.display = "block";
    });
  };

  const [chartData, setChartData] = useState(null);
  const fetchPrices = async (url, fromTime, toTime, furnace, interval) => {
    const subTitle = makeSubTitle();
    console.log(subTitle);
    url =
      url +
      "?fromTime=" +
      moment(fromTime).format("YYYY-MM-DD HH:mm:ss") +
      "&toTime=" +
      moment(toTime).format("YYYY-MM-DD HH:mm:ss") +
      "&furnace=" +
      furnace +
      "&interval=" +
      interval;
    console.log(url);
    const res = await fetch(url);
    const data = await res.json();
    setChartData({
      labels: data.data.map((item) => getTimePortion(item)),
      datasets: [
        {
          label: "Actual Process Temp",
          data: data.data.map((item) => item.actualMv),
          borderColor: "#ff6384",
          backgroundColor: "#ff6384",
          yAxisID: "y"
        },
        {
          label: "Set Process Temp",
          data: data.data.map((item) => item.setMv),
          borderColor: "#ff6384",
          backgroundColor: "#ff6384",
          yAxisID: "y"
        },
        {
          label: "Actual Oil Temp",
          data: data.data.map((item) => item.actualOt),
          borderColor: "#36a2eb",
          backgroundColor: "#36a2eb",
          yAxisID: "y"
        },
        {
          label: "Set Oil Temp",
          data: data.data.map((item) => item.setOt),
          borderColor: "#36a2eb",
          backgroundColor: "#36a2eb",
          yAxisID: "y"
        },
        {
          label: "Actual % CP",
          data: data.data.map((item) => item.actualCp),
          borderColor: "#cc65fe",
          backgroundColor: "#cc65fe",
          yAxisID: "y1"
        },
        {
          label: "Set % CP",
          data: data.data.map((item) => item.setCp),
          borderColor: "#cc65fe",
          backgroundColor: "#cc65fe",
          yAxisID: "y1"
        }
      ]
    });
  };

  // useEffect(() => {
  //   //fetchPrices();
  // }, [setChartData]);

  // const [url, setUrl] = useState(null);

  return (
    <div className="App">
      <div className="App FormDiv">
        <br />
        <div className="DatePicker">
          From{" "}
          <DatePicker
            selected={fromTime}
            onChange={handleChangeFromTime}
            dateFormat="yyyy-MM-dd HH:mm:ss"
            timeInputLabel="Time:"
            timeFormat="HH:mm"
            startDate={fromTime}
            endDate={toTime}
            showTimeSelect
          />
          <br />
          To{" "}
          <DatePicker
            selected={toTime}
            onChange={handleChangeToTime}
            dateFormat="yyyy-MM-dd HH:mm:ss"
            timeInputLabel="Time:"
            timeFormat="HH:mm:ss"
            startDate={fromTime}
            endDate={toTime}
            minDate={fromTime}
            showTimeSelect
          />
        </div>
        <div>
          Furnace
          <select
            className="select"
            value={furnace}
            onChange={(e) => {
              setFurnace(e.target.value);
            }}
          >
            {options.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label} = ({option.value})
              </option>
            ))}
          </select>
          <br />
          Customer{" "}
          <input
            className="input10"
            onChange={(e) => {
              setCustomer(e.target.value);
            }}
          />
          <br />
          Material{" "}
          <input
            className="input5"
            onChange={(e) => {
              setMaterial(e.target.value);
            }}
          />
          <br />
          Part Name{" "}
          <input
            className="input5"
            onChange={(e) => {
              setPartName(e.target.value);
            }}
          />
          <br />
          Part No.{" "}
          <input
            className="input5"
            onChange={(e) => {
              setPartNumber(e.target.value);
            }}
          />
          <br />
          Process{" "}
          <input
            className="input5"
            onChange={(e) => {
              setProcess(e.target.value);
            }}
          />
          <br />
          <br />
          Batch Qty.{" "}
          <input
            className="input5"
            onChange={(e) => {
              setBatchQty(e.target.value);
            }}
          />
          <br />
          Batch No.{" "}
          <input
            className="input5"
            onChange={(e) => {
              setBatchNo(e.target.value);
            }}
          />
          <br />
          Soaking Start{" "}
          <input
            className="input5"
            onChange={(e) => {
              setSoakingStart(e.target.value);
            }}
          />
          <br />
          Soaking End{" "}
          <input
            className="input5"
            onChange={(e) => {
              setSoakingEnd(e.target.value);
            }}
          />
          <br />
          Total Soaking Time{" "}
          <input
            className="input5"
            onChange={(e) => {
              setTotalSoakingTime(e.target.value);
            }}
          />
          <br />
          Interval{" "}
          <input
            type="number"
            className="input4"
            onChange={(e) => {
              setInterval(e.target.value);
            }}
          />
          <br />
          {/* {fromTime && toTime && furnace ? ( */}
          <div>
            <button
              className="button"
              onClick={() =>
                fetchPrices(
                  "http://localhost:8082/data",
                  fromTime,
                  toTime,
                  furnace,
                  interval
                )
              }
            >
              Draw
            </button>
            <button className="button" onClick={(e) => exportChart(e)}>
              Export
            </button>
            <button
              className="button"
              onClick={() =>
                setDisplayValues((displayValues) => !displayValues)
              }
            >
              Reading
            </button>{" "}
          </div>
          {/* ) : null} */}
          <button
            className="button"
            onClick={() =>
              fetchPrices(
                "https://mocki.io/v1/846e861f-8566-4006-bb21-59cac21a1fff"
              )
            }
          >
            Data B
          </button>
          <button className="button" onClick={() => setChartData({})}>
            Reset
          </button>
        </div>
      </div>
      <div className="App ChartDiv">
        <Chart2
          chartData={chartData}
          displayValues={displayValues}
          subtitle={subTitle ? subTitle : ""}
        />
      </div>
    </div>
  );
}
